// import axios from "axios";
import axios from "axios";
import MDBox from "components/MDBox";
import Footer from "examples/Footer";
import DashboardLayout from "examples/LayoutContainers/DashboardLayout";
import DashboardNavbar from "examples/Navbars/DashboardNavbar";

import { useEffect, useState } from "react";
import { Map, MapMarker } from "react-kakao-maps-sdk";

const API_KEY = "11f51a135860cbff7041b080fabf611f";

function Weather() {
  const [state, setState] = useState({
    center: {
      lat: 33.450701,
      lng: 126.570667,
    },
    errMsg: null,
    isLoading: true,
  });
  const [temp, setTemp] = useState("");
  const [weather, setWeather] = useState("");

  useEffect(() => {
    if (navigator.geolocation) {
      // GeoLocation    
      navigator.geolocation.getCurrentPosition(
        (position) => {
          setState((prev) => ({
            ...prev,
            center: {
              lat: position.coords.latitude, // 
              lng: position.coords.longitude, // 
            },
            isLoading: false,
          }));
          axios
            .get(
              `https://api.openweathermap.org/data/2.5/weather?lat=${state.center.lat}&lon=${state.center.lng}&appid=${API_KEY}`
            )
            .then((res) => {
              console.log(`You live in ${state.center.lat} and ${state.center.lng}`);
              console.log(
                ` : ${Math.round(res.data.main.temp - 273.15)},  : ${
                  res.data.weather[0].main
                }`
              );
              setTemp(Math.round(res.data.main.temp - 273.15));
              setWeather(res.data.weather[0].main);
            });
        },
        (err) => {
          setState((prev) => ({
            ...prev,
            errMsg: err.message,
            isLoading: false,
          }));
        }
      );
    } else {
      // HTML5 GeoLocation         
      setState((prev) => ({
        ...prev,
        errMsg: "geolocation  ..",
        isLoading: false,
      }));
    }
  }, []);

  return (
    <DashboardLayout>
      <DashboardNavbar />

      <MDBox>  : {state.center.lat}</MDBox>
      <MDBox>  : {state.center.lng}</MDBox>
      <MDBox>  : {temp}</MDBox>
      <MDBox>  : {weather}</MDBox>
      <Map //   Container
        center={state.center}
        style={{
          //  
          width: "100%",
          height: "450px",
        }}
        level={3} //   
      >
        {!state.isLoading && (
          <MapMarker position={state.center}>
            <div style={{ padding: "5px", color: "#000" }}>
              {state.errMsg ? state.errMsg : " "}
            </div>
          </MapMarker>
        )}
      </Map>
      <Footer />
    </DashboardLayout>
  );
}

export default Weather;
